:root {
    --primary-font: 'Montserrat', sans-serif;
    --secondary-font: 'Seymour One', sans-serif;
}

h1,
h2,
h3,
h4
p {
    margin-top: 0;
    margin-bottom: 0;
}

ul,
ol {
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

a {
    text-decoration: none;
}

img {
    display: block;
}